.Task-view{
    padding-left: 20vw;
    background-color: rgb(217, 233, 250);
    .wrapper{
        padding: 30px;
        .modal-contr{
            z-index: 10;
            position: fixed;
            top: 0;
            left: 0;
            width: 100%;
            height: 100vh;
            background-color: rgba(8, 8, 8, 0.384);
            transition: 0.3s;
            -webkit-transition: 0.3s;
            -moz-transition: 0.3s;
            -ms-transition: 0.3s;
            -o-transition: 0.3s;
            .card{
                width: 40vw;
                .card-header{
                    background-color: white;
                    padding: 20px;
                    .fa-globe{
                        margin-top: -30px;
                        background-color: rgb(203, 227, 251);
                        color: rgb(0, 114, 228);
                        padding: 15px;
                        font-size: 30px;
                        border-radius: 12px;
                        -webkit-border-radius: 12px;
                        -moz-border-radius: 12px;
                        -ms-border-radius: 12px;
                        -o-border-radius: 12px;
                    }
                }
                .card-body{
                    padding: 20px;
                }
                .card-footer{
                    background-color: white;
                    padding: 20px;
                    .close{
                        padding: 5px 20px;
                        font-size: 18px;
                        font-weight: 600;
                        border: solid 1px rgb(184, 184, 184);
                        &:hover{
                            background-color: rgb(230, 230, 230);
                        }
                    }
                    .share{
                        border: none;
                        padding: 5px 20px;
                        font-size: 18px;
                        font-weight: 600;
                        color: white; 
                        background-color: rgb(14, 170, 66);                      
                        &:hover{
                            background-color: rgb(6, 139, 50);
                        }
                    }
                }
            }
        }
        .create-contr{
            z-index: 10;
            position: fixed;
            top: 0;
            left: 0;
            width: 100%;
            height: 100vh;
            background-color: rgba(8, 8, 8, 0.384);
            transition: 0.3s;
            -webkit-transition: 0.3s;
            -moz-transition: 0.3s;
            -ms-transition: 0.3s;
            -o-transition: 0.3s;
            .card{
                width: 30vw;
                height: 100vh;
                .card-header{                    
                    padding: 20px;
                    .fa-times{
                        font-size: 22px;
                        cursor: pointer;
                        font-weight: 400;
                    }
                }
                .card-body{
                    padding: 20px;
                    overflow-y: scroll;
                }
                .card-footer{                    
                    padding: 20px;
                    .close{
                        padding: 5px 20px;
                        font-size: 18px;
                        font-weight: 600;
                        background-color: white;
                        border: solid 1px rgb(184, 184, 184);
                        &:hover{
                            background-color: rgb(230, 230, 230);
                        }
                    }
                    .save{
                        border: none;
                        padding: 5px 20px;
                        font-size: 18px;
                        font-weight: 600;
                        color: white; 
                        background-color: rgb(14, 100, 170);                      
                        &:hover{
                            background-color: rgb(8, 80, 139);
                        }
                    }
                }
            }
        }
        .share{
            padding: 5px 20px;
            background-color: white;
            font-size: 18px;
            font-weight: 600;
            border: solid 1px rgb(189, 189, 189);
            &:hover{
                background-color: rgb(230, 230, 230);
            }
        }
        .create{
            padding: 5px 20px;
            background-color: rgb(0, 114, 228);
            color: white;
            font-size: 18px;
            font-weight: 600;
            border: none;
            &:hover{
                background-color: rgb(1, 93, 185);
            }
        }
        .c-1{  
            .col-md-8{                
                .orders{
                    padding: 20px;
                    background-color: white;
                    height: 100%;
                    border-radius: 7px;
                    -webkit-border-radius: 7px;
                    -moz-border-radius: 7px;
                    -ms-border-radius: 7px;
                    -o-border-radius: 7px;
                    .btn{
                        font-size: 18px;
                        font-weight: 600;
                        border: solid 1px rgb(173, 173, 173);
                        &:hover{
                            background-color: rgb(217, 233, 250);
                        }
                    }
                } 
            }         
            .perfor{
                padding: 20px;
                background-color: white;
                border-radius: 7px;
                -webkit-border-radius: 7px;
                -moz-border-radius: 7px;
                -ms-border-radius: 7px;
                -o-border-radius: 7px;
                .col-md-12{
                    padding: 20px;
                    .prof{                        
                        img{
                            margin-right: 20px;
                            width: 60px;
                            height: 60px;
                            border-radius: 50%;
                            -webkit-border-radius: 50%;
                            -moz-border-radius: 50%;
                            -ms-border-radius: 50%;
                            -o-border-radius: 50%;
                        }
                    }
                }
            }
        }
        .c-2{
            .col-md-4{
                .budget{
                    padding: 20px;
                    background-color: white;
                    border-radius: 10px;
                    -webkit-border-radius: 10px;
                    -moz-border-radius: 10px;
                    -ms-border-radius: 10px;
                    -o-border-radius: 10px;
                    .fa-credit-card{
                        background-color: rgb(241, 57, 88);
                        color: white;
                        padding: 20px;
                        font-size: 22px;
                        border-radius: 50%;
                        -webkit-border-radius: 50%;
                        -moz-border-radius: 50%;
                        -ms-border-radius: 50%;
                        -o-border-radius: 50%;
                    }
                    .fa-arrow-up{
                        padding: 7px 10px;
                        background-color: rgb(141, 214, 202);
                        color: rgb(0, 128, 79);
                        border-radius: 7px;
                        -webkit-border-radius: 7px;
                        -moz-border-radius: 7px;
                        -ms-border-radius: 7px;
                        -o-border-radius: 7px;
                    }
                }
            }
            .col-md-4{
                .new-projects{
                    padding: 20px;
                    background-color: white;
                    border-radius: 10px;
                    -webkit-border-radius: 10px;
                    -moz-border-radius: 10px;
                    -ms-border-radius: 10px;
                    -o-border-radius: 10px;
                    .fa-users{
                        background-color: rgb(23, 94, 248);
                        color: white;
                        padding: 20px;
                        border-radius: 50%;
                        -webkit-border-radius: 50%;
                        -moz-border-radius: 50%;
                        -ms-border-radius: 50%;
                        -o-border-radius: 50%;
                        font-size: 22px;
                    }
                    .fa-arrow-up{
                        padding: 7px 10px;
                        background-color: rgb(141, 214, 202);
                        color: rgb(0, 128, 79);
                        border-radius: 7px;
                        -webkit-border-radius: 7px;
                        -moz-border-radius: 7px;
                        -ms-border-radius: 7px;
                        -o-border-radius: 7px;
                    }
                }
            }
            .col-md-4{
                .total-hours{
                    padding: 20px;
                    background-color: white;
                    border-radius: 10px;
                    -webkit-border-radius: 10px;
                    -moz-border-radius: 10px;
                    -ms-border-radius: 10px;
                    -o-border-radius: 10px;
                    .fa-clock-o{
                        background-color: rgb(1, 224, 195);
                        color: white;
                        padding: 20px;
                        border-radius: 50%;
                        -webkit-border-radius: 50%;
                        -moz-border-radius: 50%;
                        -ms-border-radius: 50%;
                        -o-border-radius: 50%;
                        font-size: 22px;
                    }
                    .fa-arrow-down{
                        padding: 7px 10px;
                        background-color: rgb(250, 185, 250);
                        color: rgb(255, 10, 63);
                        border-radius: 7px;
                        -webkit-border-radius: 7px;
                        -moz-border-radius: 7px;
                        -ms-border-radius: 7px;
                        -o-border-radius: 7px;                        
                    }
                }
            }
        }
    }
}

