.Detail-view{
    padding-left: 20vw;
    background-color: rgb(217, 233, 250);
    .wrapper{
        padding: 30px;
        .form{
            padding: 0 150px;
            label{
                font-size: 20px;
                font-weight: 600;
            }
            input{
                padding: 15px;
            }
            textarea{
                padding: 15px;
            }
            select{
                padding: 15px;                
            }
            .save{
                border: none;
                padding: 10px 50px;
                font-size: 22px;
                font-weight: 600;
                color: white; 
                background-color: rgb(14, 100, 170);                      
                &:hover{
                    background-color: rgb(8, 80, 139);
                }
            }
        }
    }
}

