.Profile{
    padding-left: 20vw;
    background-color: rgb(217, 233, 250);
    @media(max-width:1192px){
        padding-left: 25vw;
    }
    @media(max-width:990px){
        padding-left: 0;
        padding-top: 70px;
    }
    .wrapper{
        padding: 30px;
        @media(max-width:990px){
            padding: 30px 60px;
        }
        @media(max-width:810px){
            padding: 30px;
        }
        @media(max-width:767px){
            padding: 30px 150px;
        }
        @media(max-width:580px){
            padding: 30px 120px;
        }
        @media(max-width:550px){
            padding: 30px 100px;
        }
        @media(max-width:500px){
            padding: 30px 80px;
        }
        @media(max-width:470px){
            padding: 30px 60px;
        }
        @media(max-width:430px){
            padding: 30px 45px;
        }
        @media(max-width:400px){
            padding: 30px 35px;
        }
        @media(max-width:380px){
            padding: 30px;
        }
        @media(max-width:290px){
            padding: 20px;
        }
        @media(max-width:250px){
            padding: 10px;
        }
        .u-1{
            height: 50vh;
            overflow: hidden;
            img{
                object-fit: cover;
            }
        }
        .contr-prof{
            overflow-y: auto;
            padding: 30px 0;
            z-index: 2000001;
            position: fixed;
            top: 0;
            left: 0;
            display: flex;
            align-items: center;
            justify-content: center;
            width: 100%;
            height: 100vh;
            background-color: rgba(8, 8, 8, 0.514);
            transition: 0.3s;
            -webkit-transition: 0.3s;
            -moz-transition: 0.3s;
            -ms-transition: 0.3s;
            -o-transition: 0.3s;
            .edit-profile{
                background-color: white;
                padding: 20px; 
                height: 90vh;
                overflow-y: scroll;
                border-radius: 10px;
                -webkit-border-radius: 10px;
                -moz-border-radius: 10px;
                -ms-border-radius: 10px;
                -o-border-radius: 10px;
                @media(max-width:950px){
                    width: 70%;
                }
                @media(max-width:620px){
                    width: 90%;
                }
                .pro{
                    flex-wrap: wrap;
                }
                .proff{
                    padding: 0 5px;                   
                    .text-center{
                        cursor: pointer;
                        background-color: rgb(184, 184, 184);
                        font-weight: 600;
                        font-size: 14px;
                        padding: 5px;
                        border-radius: 5px;
                        -webkit-border-radius: 5px;
                        -moz-border-radius: 5px;
                        -ms-border-radius: 5px;
                        -o-border-radius: 5px;
                        @media(max-width:1080px){
                            font-weight: 500;
                            font-size: 12px;
                        }
                        @media(max-width:950px){
                            font-weight: 600;
                            font-size: 14px;
                            padding: 5px;
                        }                        
                    }
                }
                .add{
                    border: none;
                    padding: 5px 20px;
                    font-size: 18px;
                    font-weight: 600;
                    color: white; 
                    background-color: rgb(16, 112, 190);                      
                    &:hover{
                        background-color: rgb(9, 92, 160);
                    }
                }
                .close{
                    padding: 5px 20px;
                    font-size: 18px;
                    font-weight: 600;
                    border: solid 1px rgb(184, 184, 184);
                    &:hover{
                        background-color: rgb(230, 230, 230);
                    }
                }
                .save{
                    border: none;
                    padding: 5px 20px;
                    font-size: 18px;
                    font-weight: 600;
                    color: white; 
                    background-color: rgb(16, 112, 190);                      
                    &:hover{
                        background-color: rgb(9, 92, 160);
                    }
                }
            }
        }
        .bio{
            --bs-gutter-x: 0;
            display: flex;
            justify-content: center;
            align-items: flex-end;
            @media(max-width:767px){
                text-align: center;
            }
            img{
                border: solid 5px crimson;
                width: 100%;
                height: 35vh;
                overflow: hidden;
                border-radius: 50%;
                -webkit-border-radius: 50%;
                -moz-border-radius: 50%;
                -ms-border-radius: 50%;
                -o-border-radius: 50%;
                @media(max-width:1130px){
                    height: 30vh;
                }
                @media(max-width:990px){
                    height: 35vh;
                }
                @media(max-width:900px){
                    height: 30vh;
                }
                @media(max-width:767px){
                    width: 90%;
                    height: 65vh;
                }
                @media(max-width:710px){
                    width: 100%;
                    height: 65vh;
                }
                @media(max-width:650px){
                    width: 100%;
                    height: 60vh;
                }
                @media(max-width:620px){
                    width: 100%;
                    height: 55vh;
                }
                @media(max-width:370px){
                    width: 100%;
                    height: 50vh;
                }
                @media(max-width:345px){
                    width: 100%;
                    height: 45vh;
                }
                @media(max-width:320px){
                    width: 100%;
                    height: 40vh;
                }
                @media(max-width:270px){
                    width: 100%;
                    height: 35vh;
                }
            }
            .proff{
                padding: 0 5px;
                @media(max-width:767px){
                    display: flex;
                    justify-content: center;
                }
                .text-center{
                    background-color: white;
                    font-weight: 600;
                    font-size: 14px;
                    padding: 0 5px;
                    border-radius: 5px;
                    -webkit-border-radius: 5px;
                    -moz-border-radius: 5px;
                    -ms-border-radius: 5px;
                    -o-border-radius: 5px;
                    @media(max-width:1130px){
                        font-weight: 500;
                        font-size: 12px;
                    }
                    @media(max-width:990px){
                        font-weight: 600;
                        font-size: 14px;
                    }
                    @media(max-width:900px){
                        font-weight: 500;
                        font-size: 12px;
                    }
                    @media(max-width:767px){
                        font-weight: 600;
                        font-size: 16px;
                        padding: 10px 5px;
                        width: 60%;
                    }
                    @media(max-width:620px){                        
                        width: 70%;
                    }
                    @media(max-width:312px){                        
                        width: 90%;
                    }
                }
            }
            .col-md-5{
                .edit{
                    border: none;
                    padding: 10px 20px;
                    font-size: 18px;
                    font-weight: 600;
                    color: white; 
                    background-color: rgb(16, 112, 190);                      
                    @media(max-width:265px){
                        margin-top: 20px;
                    }
                    @media(max-width:250px){
                        margin-top: 0px;
                    }
                    @media(max-width:245px){
                        margin-top: 20px;
                    }
                    &:hover{
                        background-color: rgb(9, 92, 160);
                    }
                }
                .notificarion{
                    padding: 10px 15px;
                    font-size: 18px;
                    font-weight: 600;
                    background-color: white;
                    border: solid 1px rgb(184, 184, 184);
                    &:hover{
                        background-color: rgb(230, 230, 230);
                    }
                }
            }
        }
        .col-md-8{
            .bio-text{
                height: 100%;
                background-color: white;
                padding: 20px;
                text-align: justify;
                border-radius: 7px;
                -webkit-border-radius: 7px;
                -moz-border-radius: 7px;
                -ms-border-radius: 7px;
                -o-border-radius: 7px;
            }
        }
        .location{
            background-color: white;
            padding: 20px;
            border-radius: 7px;
            -webkit-border-radius: 7px;
            -moz-border-radius: 7px;
            -ms-border-radius: 7px;
            -o-border-radius: 7px;
        }
        .social{
            background-color: white;
            padding: 20px;
            border-radius: 7px;
            -webkit-border-radius: 7px;
            -moz-border-radius: 7px;
            -ms-border-radius: 7px;
            -o-border-radius: 7px;
        }
        .file{
            cursor: pointer;
            padding: 15px;
            background-color: white;
            border-radius: 7px;
            -webkit-border-radius: 7px;
            -moz-border-radius: 7px;
            -ms-border-radius: 7px;
            -o-border-radius: 7px;
            img{
                width: 100%;
                // height: 90%;
                border-radius: 7px;
                -webkit-border-radius: 7px;
                -moz-border-radius: 7px;
                -ms-border-radius: 7px;
                -o-border-radius: 7px;
            }
        }
    }
}