*{
    padding: 0;
    margin: 0;
}
.Navbar{
    z-index: 10002;
    padding: 20px 0;
    width: 20vw;
    background-color: white;
    border-right: solid 1px rgb(190, 190, 190);
    text-align: center;
    position: fixed;
    display: block;
    height: 100vh;
    overflow-y: auto;
    transition: 0.3s;
    -webkit-transition: 0.3s;
    -moz-transition: 0.3s;
    -ms-transition: 0.3s;
    -o-transition: 0.3s;
    @media(max-width:1192px){
        width: 25vw;
    }
    @media(max-width:990px){
        left: -300px;
        width: 30vw;
    }
    @media (max-width:810px){
        width: 35vw;
    }
    @media (max-width:720px){
        width: 40vw;
    }
    @media (max-width:630px){
        width: 45vw;
    }
    @media (max-width:570px){
        width: 50vw;
    }
    @media (max-width:500px){
        width: 55vw;
    }
    @media (max-width:450px){
        width: 62vw;
        height: 110vh;
    }
    @media (max-width:400px){
        width: 70vw;
        height: 115vh;
    }
    @media (max-width:350px){
        width: 77vw;
        height: 125vh;
    }
    @media (max-width:320px){
        width: 85vw;
        height: 135vh;
    }
    @media (max-width:290px){
        width: 95vw;
        height: 145vh;
    }
    @media (max-width:260px){
        width: 100vw;
        height: 145vh;
    }
    .fa-times{
        font-size: 30px;
        position: absolute;
        top: 10px;
        right: 15px;
        color: rgb(87, 87, 87);
        @media(min-width:990.1px){
            display: none;
        }
    }
    .navbar-brand{
        color: dodgerblue;
        font-size: 35px;
        font-weight: 600;
    }
    .navbar-na{
        padding: 0;
    }
    .nav-item{
        font-size: 19px;
        font-weight: 600;
        // padding: 0 20px;
        &:hover{
            background-color: rgb(217, 233, 250);
            .nav-link{
                padding-left: 30px;
                transition: 0.3s;
                -webkit-transition: 0.3s;
                -moz-transition: 0.3s;
                -ms-transition: 0.3s;
                -o-transition: 0.3s;
            }
        }
    }
    li{
        padding-right: 20px;
        text-align: left;
        display: block;
    }
    a{
        text-decoration: none;
        color: rgb(53, 53, 53);
    }
    .dropdown-men{
        transition: 0.3s;
        -webkit-transition: 0.3s;
        -moz-transition: 0.3s;
        -ms-transition: 0.3s;
        -o-transition: 0.3s;
    }
}
.Navbar1{
    width: 100%;
    z-index: 10001;
    position: fixed;
    @media(min-width:990.1px){
        display: none;
    }
    .navbar{
        padding: 10px 40px;

        .fa-bars{
            color: rgb(88, 87, 87);
            font-size: 25px;
        }
    }
}

.show-navbar{
    left: 0;
}