body{
    background-color: rgb(193, 222, 251);
}

.row{
    --bs-gutter-x: 0;
}

.Project-overview{
    padding-left: 20vw;
    background-color: rgb(217, 233, 250);
    @media(max-width:1192px){
        padding-left: 25vw;
    }
    @media(max-width:990px){
        padding-left: 0;
        padding-top: 70px;
    }
    .wrapper{
        padding: 30px;
        @media(max-width:767px){
            padding: 30px 60px;
        }
        @media(max-width:420px){
            padding: 30px;
        }
        @media(max-width:310px){
            padding: 30px 20px;
        }
        .modal-contr{
            z-index: 2000001;
            position: fixed;
            top: 0;
            left: 0;
            width: 100%;
            height: 100vh;
            background-color: rgba(8, 8, 8, 0.384);
            transition: 0.3s;
            -webkit-transition: 0.3s;
            -moz-transition: 0.3s;
            -ms-transition: 0.3s;
            -o-transition: 0.3s;
            .card{
                width: 40vw;
                @media(max-width:920px){
                    width: 60vw;
                }
                @media(max-width:630px){
                    width: 80vw;
                }
                @media(max-width:460px){
                    width: 95vw;
                }
                .card-header{
                    background-color: white;
                    padding: 20px;
                    .fa-globe{
                        margin-top: -30px;
                        background-color: rgb(203, 227, 251);
                        color: rgb(0, 114, 228);
                        padding: 15px;
                        font-size: 30px;
                        border-radius: 12px;
                        -webkit-border-radius: 12px;
                        -moz-border-radius: 12px;
                        -ms-border-radius: 12px;
                        -o-border-radius: 12px;
                    }
                }
                .card-body{
                    padding: 20px;
                }
                .card-footer{
                    background-color: white;
                    padding: 20px;
                    @media(max-width:315px){                            
                        padding: 20px 10px;
                    }
                    a{
                        @media(max-width:290px){                            
                            font-size: 14px;
                        }
                        @media(max-width:245px){                            
                            font-size: 12px;
                        }
                    }
                    .fa-clipboard{
                        @media(max-width:315px){                            
                            font-size: 14px;
                        }
                        @media(max-width:290px){                            
                            font-size: 12px;
                        }
                    }
                    .close{
                        padding: 5px 20px;
                        font-size: 18px;
                        font-weight: 600;
                        border: solid 1px rgb(184, 184, 184);
                        @media(max-width:385px){
                            padding: 5px 15px;
                            font-size: 16px;
                        }
                        @media(max-width:350px){
                            padding: 5px 10px;
                            font-size: 14px;
                        }
                        @media(max-width:290px){
                            padding: 5px;                            
                            font-size: 12px;
                        }
                        &:hover{
                            background-color: rgb(230, 230, 230);
                        }
                    }
                    .share{
                        border: none;
                        padding: 5px 20px;
                        font-size: 18px;
                        font-weight: 600;
                        color: white; 
                        background-color: rgb(14, 170, 66);                      
                        @media(max-width:385px){
                            padding: 5px 15px;
                            font-size: 16px;
                        }
                        @media(max-width:350px){
                            padding: 5px 10px;
                            font-size: 14px;
                        }
                        @media(max-width:290px){                            
                            font-size: 12px;
                            padding: 5px;
                        }
                        &:hover{
                            background-color: rgb(6, 139, 50);
                        }
                    }
                }
            }
        }
        .create-contr{
            z-index: 2000001;
            position: fixed;
            top: 0;
            left: 0;
            width: 100%;
            height: 100vh;
            background-color: rgba(8, 8, 8, 0.384);
            transition: 0.3s;
            -webkit-transition: 0.3s;
            -moz-transition: 0.3s;
            -ms-transition: 0.3s;
            -o-transition: 0.3s;
            .card{
                width: 30vw;
                height: 100vh;
                @media(max-width:850px){
                    width: 50vw;
                }
                @media(max-width:525px){
                    width: 70vw;
                }
                @media(max-width:380px){
                    width: 100vw;
                }
                .card-header{                    
                    padding: 20px;
                    h5{
                        @media(max-width:260px){
                            font-size: 16px;
                        }
                    }
                    .fa-times{
                        font-size: 22px;
                        cursor: pointer;
                        font-weight: 400;
                    }
                }
                .card-body{
                    padding: 20px;
                    overflow-y: scroll;
                }
                .card-footer{                    
                    padding: 20px;
                    .close{
                        padding: 5px 20px;
                        font-size: 18px;
                        font-weight: 600;
                        background-color: white;
                        border: solid 1px rgb(184, 184, 184);
                        &:hover{
                            background-color: rgb(230, 230, 230);
                        }
                    }
                    .save{
                        border: none;
                        padding: 5px 20px;
                        font-size: 18px;
                        font-weight: 600;
                        color: white; 
                        background-color: rgb(14, 100, 170);                      
                        &:hover{
                            background-color: rgb(8, 80, 139);
                        }
                    }
                }
            }
        }
        .share{
            padding: 5px 20px;
            background-color: white;
            font-size: 18px;
            font-weight: 600;
            border: solid 1px rgb(189, 189, 189);
            @media(max-width:460px){
                font-size: 16px;
            }
            &:hover{
                background-color: rgb(230, 230, 230);
            }
        }
        .create{
            padding: 5px 20px;
            background-color: rgb(0, 114, 228);
            color: white;
            font-size: 18px;
            font-weight: 600;
            border: none;
            @media(max-width:460px){
                font-size: 16px;
            }
            &:hover{
                background-color: rgb(1, 93, 185);
            }
        }
        .c{ 
            display: flex;
            @media(max-width:360px){
                display: block;
            }
            h3{
                @media(max-width:460px){
                    font-size: 16px;
                }
            }
        }
        .navbar{
            padding: 0;
            border-bottom: solid 1px rgb(187, 187, 187);
        }
        .navbar-na{
            display: flex;
            align-items: center;
            justify-content: start;
            @media(max-width:333px){
                display: none;
            }
            .nav-item{
                list-style: none;
                margin-right: 15px;
                @media(max-width:495px){                   
                    padding: 0;
                }
                @media(max-width:350px){                   
                    padding: 0;
                    margin-right: 10px;
                }
                a{
                    @media(max-width:767px){
                        font-size: 16px;
                    }
                    @media(max-width:495px){
                        font-size: 14px;
                        padding: 0;
                    }
                    @media(max-width:375px){
                        font-size: 12px;
                        padding: 0;
                    }
                }
            }
        }
        .c-1{  
            .col-md-8{                
                .orders{
                    padding: 20px;
                    background-color: white;
                    height: 100%;
                    border-radius: 7px;
                    -webkit-border-radius: 7px;
                    -moz-border-radius: 7px;
                    -ms-border-radius: 7px;
                    -o-border-radius: 7px;
                    .btn{
                        font-size: 18px;
                        font-weight: 600;
                        border: solid 1px rgb(173, 173, 173);
                        &:hover{
                            background-color: rgb(217, 233, 250);
                        }
                    }
                } 
            }         
            .perfor{
                padding: 20px;
                background-color: white;
                border-radius: 7px;
                -webkit-border-radius: 7px;
                -moz-border-radius: 7px;
                -ms-border-radius: 7px;
                -o-border-radius: 7px;
                .col-md-12{
                    padding: 20px;
                    @media(max-width:1192px){
                        font-size: 14px;
                        padding: 10px;
                    } 
                    @media(max-width:821px){
                        font-size: 12px;
                        padding: 10px;
                    } 
                    .prof{  
                        align-items: center; 
                        @media(max-width:821px){
                            font-size: 12px;
                            padding: 10px;
                        }    
                        @media(max-width:767px){
                            font-size: 16px;
                            padding: 10px;
                        } 
                        @media(max-width:355px){
                            font-size: 12px;
                            padding: 0px;
                        }                 
                        img{
                            margin-right: 20px;
                            width: 60px;
                            height: 60px;
                            border-radius: 50%;
                            -webkit-border-radius: 50%;
                            -moz-border-radius: 50%;
                            -ms-border-radius: 50%;
                            -o-border-radius: 50%;
                            @media(max-width:881px){
                                width: 40px;
                                height: 40px;
                            }
                            @media(max-width:767px){
                                width: 60px;
                                height: 60px;
                            }
                            @media(max-width:355px){
                                width: 40px;
                                height: 40px;
                            }
                        }
                    }
                }
            }
        }
        .c-2{
            .col-md-4{
                .budget{
                    padding: 20px;
                    background-color: white;
                    border-radius: 10px;
                    -webkit-border-radius: 10px;
                    -moz-border-radius: 10px;
                    -ms-border-radius: 10px;
                    -o-border-radius: 10px;
                    .fa-credit-card{
                        background-color: rgb(241, 57, 88);
                        color: white;
                        padding: 20px;
                        font-size: 22px;
                        border-radius: 50%;
                        -webkit-border-radius: 50%;
                        -moz-border-radius: 50%;
                        -ms-border-radius: 50%;
                        -o-border-radius: 50%;
                    }
                    .fa-arrow-up{
                        padding: 7px 10px;
                        background-color: rgb(141, 214, 202);
                        color: rgb(0, 128, 79);
                        border-radius: 7px;
                        -webkit-border-radius: 7px;
                        -moz-border-radius: 7px;
                        -ms-border-radius: 7px;
                        -o-border-radius: 7px;
                        @media(max-width:1192px){
                            font-size: 14px;
                        }
                    }
                }
            }
            .col-md-4{
                .new-projects{
                    padding: 20px;
                    background-color: white;
                    border-radius: 10px;
                    -webkit-border-radius: 10px;
                    -moz-border-radius: 10px;
                    -ms-border-radius: 10px;
                    -o-border-radius: 10px;
                    .fa-users{
                        background-color: rgb(23, 94, 248);
                        color: white;
                        padding: 20px;
                        border-radius: 50%;
                        -webkit-border-radius: 50%;
                        -moz-border-radius: 50%;
                        -ms-border-radius: 50%;
                        -o-border-radius: 50%;
                        font-size: 22px;
                    }
                    .fa-arrow-up{
                        padding: 7px 10px;
                        background-color: rgb(141, 214, 202);
                        color: rgb(0, 128, 79);
                        border-radius: 7px;
                        -webkit-border-radius: 7px;
                        -moz-border-radius: 7px;
                        -ms-border-radius: 7px;
                        -o-border-radius: 7px;
                        @media(max-width:1192px){
                            font-size: 14px;
                        }
                    }
                }
            }
            .col-md-4{
                .total-hours{
                    padding: 20px;
                    background-color: white;
                    border-radius: 10px;
                    -webkit-border-radius: 10px;
                    -moz-border-radius: 10px;
                    -ms-border-radius: 10px;
                    -o-border-radius: 10px;
                    .fa-clock-o{
                        background-color: rgb(1, 224, 195);
                        color: white;
                        padding: 20px;
                        border-radius: 50%;
                        -webkit-border-radius: 50%;
                        -moz-border-radius: 50%;
                        -ms-border-radius: 50%;
                        -o-border-radius: 50%;
                        font-size: 22px;
                    }
                    .fa-arrow-down{
                        padding: 7px 10px;
                        background-color: rgb(250, 185, 250);
                        color: rgb(255, 10, 63);
                        border-radius: 7px;
                        -webkit-border-radius: 7px;
                        -moz-border-radius: 7px;
                        -ms-border-radius: 7px;
                        -o-border-radius: 7px;
                        @media(max-width:1192px){
                            font-size: 14px;
                        }                        
                    }
                }
            }
        }
    }
}

