.Details{
    padding-left: 20vw;
    background-color: rgb(217, 233, 250);
}

.Create-project{
    padding-left: 20vw;
    background-color: rgb(217, 233, 250);
    @media(max-width:1192px){
        padding-left: 25vw;
    }
    @media(max-width:990px){
        padding-left: 0;
        padding-top: 70px;
    }
    .wrapper{
        padding: 30px;        
        .form{
            padding: 0 150px;
            @media(max-width:720px){
                padding: 0 80px;
            }
            @media(max-width:540px){
                padding: 0 40px;
            }
            @media(max-width:455px){
                padding: 0 20px;
            }
            @media(max-width:410px){
                padding: 0;
            }
            label{
                font-size: 20px;
                font-weight: 600;
            }
            input{
                padding: 15px;
            }
            textarea{
                padding: 15px;
            }
            select{
                padding: 15px;                
            }
            #date{
                width: 50%;
                @media(max-width:520px){
                    width: 100%;
                }
            }
            .save{
                border: none;
                padding: 10px 50px;
                font-size: 22px;
                font-weight: 600;
                color: white; 
                background-color: rgb(14, 100, 170);                      
                &:hover{
                    background-color: rgb(8, 80, 139);
                }
            }
        }
    }
}